import React from 'react'
import { Auth } from 'aws-amplify'
import UserContext from './UserContext'
import { withRouter, Link } from 'react-router-dom'
import LoaderButton from "./components/LoaderButton";
import logo from './assets/img/modshow-logo.png';
import "./ResetPassword.css";

class SignIn extends React.Component {
  state = {
    username: '',
    password: '',
    codeSent: false,
    code: "",
    confirmed: false,
    confirmPassword: "",
    isConfirming: false,
    isSendingCode: false,
    user: {},
    authCode: ''
  }
  static contextType = UserContext
  onChange = (key, value) => {
    this.setState({
      [key]: value
    })
  }

  componentDidMount() {
    const { history } = this.props
    const { updateCurrentUser } = this.context
    Auth.currentAuthenticatedUser()
    .then(user => {
      if (!user.signInUserSession) {
       // this.setState({ user, showConfirmation: true })
       console.log(user);
      } else {
        updateCurrentUser(user)
        //console.log(user);
        history.push('/dashboard')
      }        
    })
    .catch(err => console.log(err))
  }

  handleSendCodeClick = async event => {
    event.preventDefault();

    this.setState({ isSendingCode: true });

    try {
      await Auth.forgotPassword(this.state.username);
      this.setState({ codeSent: true });
    } catch (e) {
      alert(e.message);
      this.setState({ isSendingCode: false });
    }

  }

  confirmSignIn = () => {
    const { history } = this.props
    Auth.confirmSignIn(this.state.user, this.state.authCode, this.state.user.challengeName)
      .then(user => {
        history.push('/')
      })
      .catch(err => console.log('error confirming signing in...: ', err))
  }

  validateCodeForm() {
    return this.state.username.length > 0;
  }

  validateResetForm = () => {
    return (
      this.state.code.length > 0 &&
      this.state.password.length > 0 &&
      this.state.password === this.state.confirmPassword
    );
  }

  handleConfirmClick = async event => {
    event.preventDefault();
      this.setState({ isConfirming: true });

      try {
        await Auth.forgotPasswordSubmit(
          this.state.username,
          this.state.code,
          this.state.password
        );
        this.setState({ confirmed: true });
      } catch (e) {
        alert(e.message);
        this.setState({ isConfirming: false });
      }
    
  };

renderRequestCodeForm(){
  return (
    <div className="signup-form">
      <h2 className="title"> Reset Password </h2>
      <form onSubmit={this.handleSendCodeClick}>
      <div className="formContainer">
        <input
          onChange={evt => this.onChange('username', evt.target.value)}
          style={styles.input}
          placeholder='username'
          
        />
        <LoaderButton
          block
          type="submit"
          bsSize="large"
          loadingText="Sending…"
          text="Send Confirmation"
          isLoading={this.state.isSendingCode}
          disabled={!this.validateCodeForm()}
        />
      
      </div>
      </form>
    </div>
  );
}

renderConfirmationForm(){
  return (
    <div className="signup-form">
      <h2 className="title"> Confirm Password </h2>
     
      <div className="formContainer">
      <form onSubmit={this.handleConfirmClick}>
        <input
          onChange={evt => this.onChange('code', evt.target.value)}
          style={styles.input}
          placeholder='code'
          
        />
         <input
         type="password"
          onChange={evt => this.onChange('password', evt.target.value)}
          style={styles.input}
          placeholder='Password'
          
        />
        <input
          type="password"
          onChange={evt => this.onChange('confirmPassword', evt.target.value)}
          style={styles.input}
          placeholder='Confirm Password'
          
        />
           <LoaderButton
          block
          type="submit"
          bsSize="large"
          text="Confirm"
          loadingText="Confirm…"
          isLoading={this.state.isConfirming}
          disabled={!this.validateResetForm()}
        />
       </form>
      </div>
      
    </div>
  );
}
renderSuccessMessage() {
  return (
    <div className="success">
      <p>Your password has been reset.</p>
      <p>
        <Link to="/signin">
          Click here to login with your new credentials.
        </Link>
      </p>
    </div>
  );
}

render() {
  return (
    <div className="authContainer">
       <div className="authLogo"><img src={logo}/></div>
      {!this.state.codeSent
        ? this.renderRequestCodeForm()
        : !this.state.confirmed
          ? this.renderConfirmationForm()
          : this.renderSuccessMessage()}
    </div>
  )
}

}


const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center'
  },
  footer: {
    fontWeight: '600',
    padding: '0px 25px',
    textAlign: 'center',
    color: 'rgba(0, 0, 0, 0.6)'
  },
  anchor: {
    color: '#006bfc',
    cursor: 'pointer'
  },
  icon: {
    display:'inline',
    color: 'red'
  }
}

export default withRouter(SignIn)
